export default {
    name: "activemore",
    data(){
        return{
            active:[]
        }
    },
    mounted:function () {
        this.$axios.get('/find/activity_detail?activity_id=7&user_id=1').then(res=>{
            console.log(res);
            this.active=res.data.data;
        })
    }
}



